import { NcActions, NcActionButton } from '@nextcloud/vue';
import { Loading, Folder, Upload } from '../icons.js';
import { useIsPublicMixin } from '../Editor.provider.js';
import { BaseActionEntry } from './BaseActionEntry.js';
import { useMenuIDMixin } from './MenuBar.provider.js';
import { useActionAttachmentPromptMixin, useUploadingStateMixin, useActionChooseLocalAttachmentMixin } from '../Editor/MediaHandler.provider.js';
export default {
  name: 'ActionAttachmentUpload',
  components: {
    NcActions: NcActions,
    NcActionButton: NcActionButton,
    Loading: Loading,
    Folder: Folder,
    Upload: Upload
  },
  extends: BaseActionEntry,
  mixins: [useIsPublicMixin, useActionAttachmentPromptMixin, useUploadingStateMixin, useActionChooseLocalAttachmentMixin, useMenuIDMixin],
  computed: {
    icon: function icon() {
      return this.isUploadingAttachments ? Loading : this.actionEntry.icon;
    },
    isUploadingAttachments: function isUploadingAttachments() {
      return this.$uploadingState.isUploadingAttachments;
    }
  }
};